.app__works {
    flex: 1;
    width: 100%;
    flex-direction: column;
    background-color: #091d26d1;
    border-radius: 7px;
    border-style: groove;
    border-color: #26684a;
    
  }
  
  .head-text1{
    color: #ede5d469;
    margin-top: 3.5rem;
  }
  .app__work-filter {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  
    margin: 4rem 0 2rem;
  
    .app__work-filter-item {
      padding: 0.5rem 1rem;
      border-radius: 0.5rem;
      background-color: #fff;
      color: #000;
      font-weight: 800;
  
      cursor: pointer;
      transition: all 0.3s ease;
      margin: 0.5rem;
  
      &:hover {
        background-color: var(--secondary-color);
        color: #fff;
      }
  
      @media screen and (min-width: 2000px) {
        padding: 1rem 2rem;
        border-radius: 0.85rem;
      }
    }
  
    .item-active {
      background-color: var(--secondary-color);
      color: #fff;
    }
  }
  
  .app__work-portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  
    .app__work-item {
      width: 270px;
      height:500px;
      flex-direction: column;
      flex-wrap: wrap;
      
      margin: 2rem;
      padding: 1rem;
      border-radius: 0.5rem;
      background-color: #a6c7ef;
      border-style: ridge;
      border-color: #cce6b3;
      color: #000;
  
      cursor: pointer;
      transition: all 0.3s ease;
  
      &:hover {
        box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
      }
  
      @media screen and (min-width: 2000px) {
        width: 470px;
        padding: 1.25rem;
        border-radius: 0.75rem;
      }
  
      @media screen and (max-width: 300px) {
        width: 100%;
        margin: 1rem;
      }
    }
  }
  
  .app__work-img {
    width: 200px;
    height: 150px;
    // padding-top: 1rem;
  
    position: relative;
    border-radius: 0.5rem;
      object-fit: fill;
  
    
  
    @media screen and (min-width: 2000px) {
      height: 350px;
    }
  }
  
  .app__work-hover {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  
    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.3s ease;
  
    div {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: rgba(0, 0, 0, 0.5);
      color: #fff;
  
      margin: 1rem;
      font-family: var(--font-base);
      font-weight: 800;
      cursor: pointer;
      transition: all 0.3s ease;
  
      svg {
        width: 50%;
        height: 50%;
        color: var(--white-color);
      }
    }
  }
  
  .app__work-content {
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;
    flex-wrap: wrap;
  
    h4 {
      margin-top: 1rem;
      line-height: 1.5;
  
      @media screen and (min-width: 2000px) {
        margin-top: 3rem;
      }
    }
  
    .app__work-tag {
      position: absolute;
  
      padding: 0.5rem 1rem;
      border-radius: 10px;
      background-color: #fff;
      top: -25px;
    }
  }
  .p-text1{
    font-family: Quicksand,sans-serif;
    font-weight: 500;
    font-size: small;
    color: #272020;
    text-align: left;
    line-height: 150%;
    word-spacing: 125%;
    letter-spacing: 110%;
    flex-wrap: wrap;

  }

  .work-name{
    font-family: Quicksand,sans-serif;
    font-weight: 700;
    font-size: large;
    color: #3d1b1be7;
    text-align: center;
    line-height: 150%;
    word-spacing: 125%;
    letter-spacing: 125%;

  }
  .work-date{
    color: rgb(4, 63, 172);
  }
  .work-role{
    color: rgb(30, 61, 15);
  }